import React, { useEffect, useState } from 'react'
import { Link, useHistory } from 'react-router-dom';
import VisibilityOutlinedIcon from '@material-ui/icons/VisibilityOutlined';
import checkIcon from '../SignUp/icons/check.png';
import useClasses from './style';
import {
    Box,
    Paper,
    Button,
    Typography,
    InputLabel,
  } from '@material-ui/core'
import auth from 'API-Service/auth';
import { useToasts } from 'react-toast-notifications';
import { GoogleLogin } from 'react-google-login';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import AppleLogin from 'react-apple-login';
import appleImg from './icons/apple.png';
import facebookImg from './icons/facebook.png';
import googleImg from './icons/google.png';

function SignIn() {
  const classes = useClasses();
  const { addToast } = useToasts();
  const history = useHistory();
  const [showError, setShowError] = useState(false);
  const [pass, setPass] = useState('');
  const [eyeVisible, setEyeVisible] = useState(false);
  const [email, setEmail] = useState('');

  useEffect(() => {
    localStorage.removeItem('token');
  }, [])
  function validateEmail(mail) {
    if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
      setShowError(false)
    } else {
      setShowError(true)
    }
  }

  const handlePasswordChange = (e) => {
    setPass(e.target.value)
  }

  const handleEmail = (e) => {
    setEmail(e.target.value);
    validateEmail(e.target.value);
  }

  const handleEye = () => {
    setEyeVisible(!eyeVisible)
  }

  const handleLogin = () => {
    auth
      .login(email, pass, false)
      .then(response => {
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){history.push('/dashboard');}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      }) 
  }

  const handleGoogleLogin = (response) => {
    const email = response.profileObj.email;
    auth
      .login(email, 'any', true)
      .then(response => {
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){history.push('/dashboard');}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      }) 
  }

  const handleGoogleFail = (e) => {
    console.log(e)
    addToast("Google Login Failed", { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
  }

  const handleFacebookLogin = (response) => {
    console.log(response)
    const name = response.name;
    auth
      .login(name, 'any', true)
      .then(response => {
        if (response.status === 200) {
          addToast(response.message, { appearance: 'success', autoDismissTimeout: 1000, autoDismiss: true })
          setTimeout(function(){history.push('/dashboard');}, 1000);
        } else {
          addToast(response.message, { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
        }
      }) 
  }

  const handleFacebookFailure = (e) => {
    console.log(e)
    addToast("Facebook Login Failed", { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
  }

  const handleAppleLogin = (response) => {
    console.log(response)
  }

  const handleForgotPassword = () => {
    history.push('/forgot-password')
  }

  return (
    <div className={classes.root}>
        <Paper className={classes.loginFormCard}
          style={{margin: "0px"}}
        >
          <Typography className={classes.title}> Log In </Typography>
          <Box className={showError ? classes.errorBox : classes.inputGroup}>
            <InputLabel className={classes.inputLabel}> Email</InputLabel>
            <input 
              type="email"
              placeholder="Enter your email.."
              onChange={(e) => handleEmail(e)}
              className={classes.input}
              value={email}
            />
            { 
              showError && <Typography className={classes.error}> Email is not correct </Typography>
            }
          </Box>

          <Box className={classes.inputGroup}>
            <InputLabel className={classes.inputLabel}>
              Password 
            </InputLabel>
            <Box className={classes.passInputGroup}
              style={{border: `1px solid #E2E2E8`}}
            >
              <input 
                type={eyeVisible ? "text" : "password"}
                placeholder="Enter your password.."
                onChange={(e) => handlePasswordChange(e)}
                value={pass}
              />
              <VisibilityOutlinedIcon 
                className={classes.eye} 
                onClick={() => handleEye()}
                style={{color: `${eyeVisible ? "#6871EA" : ""}`}}
              />
            </Box>
          </Box>
          <Box>
            <InputLabel className={classes.inputForgotLabel} onClick={handleForgotPassword}> Forgot password? </InputLabel>
            <Button fullWidth
              className={classes.loginBtn}
              disabled={ showError || email.length == 0 || pass.length == 0}
              style={{textTransform: "capitalize"}}
              onClick={handleLogin}
            >
              Log In
            </Button>
            
          </Box>
          <Box className={classes.dividerBlock}>
              <Typography> Or </Typography>
          </Box> 
          <Box className={classes.socialBtnGrp}>
            <GoogleLogin
              clientId={process.env.REACT_APP_GOOGLE_KEY}
              onSuccess={handleGoogleLogin}
              onFailure={handleGoogleFail}
              buttonText="Google"
              cookiePolicy={'single_host_origin'}
              autoLoad={false}
              render={renderProps => (									
                <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                  <Box
                    component="img"
                    alt="Google"
                    src={googleImg}
                  />
                </Button>
              )}
            />
            <FacebookLogin
              appId={process.env.REACT_APP_FACEBOOK_KEY}
              autoLoad={false}
              callback={handleFacebookLogin}
              onFailure={handleFacebookFailure}
              render={renderProps => (																		
                <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                  <Box
                    component="img"
                    alt="Facebook"
                    src={facebookImg}
                  />
                </Button>
              )}
            />
            <AppleLogin 
              clientId={'test-app.clutchwallet.xyz'}
              redirectURI="https://test-app.clutchwallet.xyz/login"
              callback={handleAppleLogin}												
              autoLoad={false}		
              render={renderProps => (																		
                <Button container="true" onClick={renderProps.onClick} disabled={renderProps.disabled} className={classes.socialBtn}>
                  <Box
                    component="img"
                    alt="Apple"
                    src={appleImg}
                  />
                </Button>
              )}
            />
          </Box>
        </Paper>
      <Box>
        <Typography className={classes.title1}
          style={{marginTop: "2rem", display: 'flex'}}
        >
          Donâ€™t have an account yet?
          <Link to='/register' className={classes.link}> Sign Up </Link>
        </Typography>
      </Box>
    </div>
  )
}

export default SignIn